body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url('assets/bg.jpg');
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  min-height: 100vh;
  position: relative;
  color: white;
  box-sizing: border-box;
}

h1 {
  margin:0;
}

.home-section {
  position: absolute;
  top: 50%;
  left: 50%; 
  transform: translate(-50%, -50%); 
  border: 0px solid red;
  width: 800px;
  height: 400px;
  text-align: center;
}

.profile-pic {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  position: relative;
  left: 50%;
  transform: translate(-50%, -10%);
}

#greeting {
  position: relative;
  font-size: 30px;
  font-family: monospace;
  color: #e94b3cbf;
}

#name {
  position: relative;
  font-size: 60px;
  font-family: 'poppins-medium', sans-serif;
  font-weight: bold;
}

a:visited {
  color: aliceblue;
}

.social_icon{
  color: white;
  margin: 0px 20px;
  transition: all 200ms;
}

.social_icons_container {
  color: white;
  font-size: 30px;
  position: absolute;
  top: auto;
  bottom: 60px;
  text-align: center;
  width: 100%;
}

.social_icon:hover {
  color: #e94b3c; 
  transition: all 200ms;
}

.btn-more-about-me {
    padding: 10px 20px;
    border-radius: 0%;
    cursor: pointer;
    background: rgba(255, 155, 239, 0);
    border: 1px solid white;
    box-shadow: 0px 0px 100px #000000;
    color: white;
    text-decoration: none;
    font-family: monospace;
    transition: all 200ms;
}

.btn-more-about-me:hover {
  border-color: transparent;
  background: #e94b3c83;
}

.mx {
  height: 100vh;
  width: 100w;
}

#profile {
  position: absolute;
  top: 50%;
  left: 50%;
  width: max-content;
  transform: translate(-50%, -50%);
  border: 1px solid red;
}

.profile-menu {
  /* display: inline-block; */
  font-size: 30px;
  float: left;
  border: 1px solid red;
}

.profile-menu-item {
  color: white;
  border-bottom: 1px solid #bbb;
  transition: all 200ms;
  margin: 10px 10px;
}

.profile-menu-item:hover {
  border-bottom-color: #e94b3c;
  color:#e94b3c;
}

/* .active {
  
} */

.active::before{
  content: ' \2192';
  position: absolute;
  transform: translate(-100%, 0%);
}

#profile-section {
  position: relative;
  display: inline-block;
  border: 1px solid red;
  width: 800px;
  height: 500px;
  overflow-y: scroll;
}

.display-none {
  display: none;
}